import React from 'react'
import styled from 'react-emotion'
import ScaleLoader from 'react-spinners/ScaleLoader'

const MARGIN = '80px'

const Loading = styled('div')`
  margin-top: ${MARGIN};
  text-align: center;
  font-size: 30px;
`

const Wrapper = styled('div')`
  margin-top: ${MARGIN};
`

export const Licences = ({ isLoading, error, data }) => {
  if (isLoading) {
    return (
      <Loading>
        <ScaleLoader size={120} color="white" loading />
      </Loading>
    )
  }

  return (
    <Wrapper>
      <table>
        <thead>
          <tr>name</tr>
          <tr>licence</tr>
        </thead>
      </table>
    </Wrapper>
  )
}



